@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-self: center;
          align-self: center;
  margin: 1rem;
  width: calc(100% - 2rem);
}

.menu-nav {
  width: 100%;
}

.menu-nav-mobile {
  border: 1px solid black;
  background-color: hsl(var(--primary-dark-violet));
  border-radius: 10px;
  /* overlay the mobile nav and center it */
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  margin-left: 1rem;
  margin-right: 1rem;
  -webkit-animation: fadein 0.3s ease-out;
          animation: fadein 0.3s ease-out;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.nav-bar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-nav-mobile li {
  text-align: center;
  margin: 2rem 0;
}

.menu-nav-mobile a {
  text-decoration: none;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.menu-nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.menu-nav li {
  margin: 0 1rem;
}

.menu-nav a {
  text-decoration: none;
  color: hsla(var(--neutral-very-dark-blue), 0.7);
  font-weight: 700;
}

.menu-nav a:hover {
  color: hsl(var(--neutral-very-dark-blue));
}

.menu-nav a:visited {
  /* color: var(--neutral-very-dark-blue); */
}

.menu-nav hr {
  display: none;
}

.nav-bar .nav-logo {
  /* margin: 0; */
  margin-right: 2rem;
}

.menu-icon svg {
  opacity: 0.5;
}

hr {
  height: 1px;
  border: 0;
  background-color: hsla(var(--primary-dark-violet), 1);
  -webkit-filter: brightness(140%);
          filter: brightness(140%);
  width: 85%;
}

.menu-nav .nav-item-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: 2rem;
  -webkit-align-items: center;
          align-items: center;
}

.menu-nav .account-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
