}

.btn {
  border: none;
  text-align: center;
}

.btn:active {
  outline: none;
}

.btn--round {
  border-radius: 25px;
}

.btn--regular {
  border-radius: 10px;
}

/* size */

.btn--mobile {
  width: 80%;
  height: 2.5rem;
}

.btn--small {
  width: 20%;
  height: 2rem;
}

.btn--nav {
  width: 100px;
  height: 2rem;
}

.btn--medium {
  width: 200px;
  height: 3rem;
}

.btn--long {
  width: 90%;
  height: 2.5rem;
}

.cyan {
  background-color: hsl(var(--primary-cyan));
  color: white;
}

.violet {
  background-color: hsl(var(--primary-dark-violet));
  color: white;
}

.cyan:hover {
  background-color: hsla(var(--primary-cyan), 0.5);
  transition: all 0.3s ease;
}

:root {
  --primary-cyan: 180, 66%, 49%;
  --primary-dark-violet: 257, 27%, 26%;
  --secondary-red: 0, 87%, 67%;
  --neutral-gray: 0, 0%, 75%;
  --neutral-grayish-violet: 257, 7%, 63%;
  --neutral-very-dark-blue: 255, 11%, 22%;
  --neutral-very-dark-violet: 260, 8%, 14%;
  --button-hover: hsla(180, 66%, 49%, 0.9);
}

* {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  box-sizing: border-box;
}

body {
  background: linear-gradient(
    white,
    white 25%,
    hsla(0, 0%, 75%, 0.4) 25%,
    hsla(0, 0%, 75%, 0.4) 60%
